<template>
  <div>
    <input type="number" v-model="startValue" placeholder="Enter starting value" />
    <CounterView :startValue="startValue" @multipleOfFive="logCount" />
    <LoggerView ref="logger" />
  </div>
</template>

<script>
import CounterView from './components/CounterView.vue';
import LoggerView from './components/LoggerView.vue';

export default {
  components: {
    CounterView,
    LoggerView
  },
  data() {
    return {
      startValue: 0
    };
  },
  methods: {
    logCount(value) {
      this.$refs.logger.handleCountEvent(value);
    }
  }
};
</script>
