<template>
  <div>
    <button @click="increment">Increment</button>
    <span>{{ count }}</span>
    <button @click="decrement">Decrement</button>
  </div>
</template>

<script>
export default {
  props: ['startValue'],
  data() {
    return {
      count: this.startValue || 0
    };
  },
  computed: {
    doubledCount() {
      return this.count * 2;
    }
  },
  methods: {
    increment() {
      this.count++;
      this.checkMultipleOfFive();
    },
    decrement() {
      this.count--;
      this.checkMultipleOfFive();
    },
    checkMultipleOfFive() {
      if (this.count % 5 === 0) {
        this.$emit('multipleOfFive', this.count);
      }
    }
  }
};
</script>
