<template>
  <div>
    <ul>
      <li v-for="log in logs" :key="log.id">{{ log.message }}</li>
    </ul>
  </div>
</template>

<script>
export default {
  data() {
    return {
      logs: []
    };
  },
  methods: {
    handleCountEvent(value) {
      this.logs.push({ id: Date.now(), message: `Count reached multiple of 5: ${value}` });
    }
  },
  created() {
    this.$parent.$on('multipleOfFive', this.handleCountEvent);
  },
  beforeUnmount() {
    this.$parent.$off('multipleOfFive', this.handleCountEvent);
  }
};
</script>
